import React, {useEffect} from "react";
import {Container, Row, Col} from "react-bootstrap";
import {useNavigate, useParams} from "react-router-dom";
import styles from "./detail_1.module.css";
import news from "../../utils/newsList";
import {useDispatch, useSelector} from "react-redux";
import {getSingleNews, newsAction} from "../../redux/actions/newsAction";
import {Link} from "react-router-dom";
import {useTranslation} from "react-i18next";
import stylesa from "../Home/home.module.css"

const Detail = () => {
    const dispatch = useDispatch();
    const {id} = useParams();
    const {t} = useTranslation();
    const navigate = useNavigate()

    const newsdata = useSelector((state) => state.newsReducer.single);
    const loading = useSelector((state) => state.newsReducer.loading);
    const data = newsdata

    useEffect(() => {
        dispatch(getSingleNews(id));
    }, [id]);
    useEffect(() => {
        dispatch(newsAction())
    },[])
    const newsdataa = useSelector((state) => state.newsReducer.news)?.products;
    console.log("data", data);
    
    const newsData = !loading && newsdata?.products
        ? newsdata?.products.slice(0, 3).map((i) => {
            return (
                <Col sm={12} md={6} lg={3} key={i.id}>
                    <Link to={`/detail/${i.id}`}>
                        <div key={i.id} className={styles.news_item_block}>
                            <img src={i.image} alt="img"/>
                            <h3>
                                {localStorage.getItem("lang") == "am" ? i.titleHy : i.titleEn}
                            </h3>
                            <p>
                                {localStorage.getItem("lang") == "am" ? i.descriptionHy : i.descriptionHy}
                            </p>
                            <p>
                                {localStorage.getItem("lang") == "am" ? i.secondSubTextHy : i.secondSubTextEn}
                            </p>
                        </div>
                    </Link>
                </Col>
            );
        })
        : null;
    return (
        <>
            <section>
                <Container>
                    <div className={styles.detail_title}>
                        <div className={styles.detail_info}>
                            <div className={styles.line}></div>
                            <h3>{t("ourN")}</h3>
                        </div>
                    </div>

                    {data
                        ? data.map((data) => {
                            return (
                                <>
                                    <div className={styles.image_slice}>
                                        <img src={data?.image} alt="image"/>
                                    </div>
                                    <div className={styles.title_slice}>
                                        <p className={styles.title}>
                                            <h3 className={styles.title_text}>
                                                {localStorage.getItem("lang") == "am"
                                                    ? data.titleHy
                                                    : data.titleEn}
                                            </h3>
                                        </p>
                                    </div>
                                    <div className={styles.description_slice}>
                                        <p>
                                            {localStorage.getItem("lang") == "am"
                                                ? data.titleHy
                                                : data.titleEn}
                                        </p>
                                    </div>
                                </>
                            );
                        })
                        : null}
                </Container>
            </section>

            <section className={stylesa.section}>
            <Container>
                    <h2 className={stylesa.red_news_title}>{t("redNews")}</h2>
                    <Row>
                        {
                            newsdataa && newsdataa?.slice(0, 4).map(i => {
                                return (
                                    <Col sm={12} md={6} lg={3} onClick={()=>navigate(`/detail/${i.id}`)}>
                                        <div className={stylesa.news_block}>
                                            <div className={stylesa.news_img_block}>
                                                <img
                                                    src={i.image}
                                                    alt="image"
                                                />
                                            </div>
                                            <h5 className={stylesa.news_title}>
                                                {localStorage.getItem("lang") == "am "
                                                    ? i.titleHy
                                                    : i.titleEn}
                                            </h5>
                                            <p className={stylesa.news_text}>
                                                {localStorage.getItem("lang") == "am "
                                                    ? i.descriptionHy.substr(0, 50)
                                                    : i.descriptionEn.substr(0, 50)}...
                                            </p>
                                        </div>
                                    </Col>
                                )
                            })
                        }
                    </Row>
                </Container>
            </section>
        </>
    );
};

export default Detail;
